//
// Layout Config
//

// Root Font Sizes
$root-font-size: 13px; // Root font size for desktop mode
$root-font-size-lg: 13px; // Root font size for tablet mode
$root-font-size-md: 12px; // Root font size for mobile mode

// Layout Theme Colors
$layout-themes: (
  dark: #00053e,
  // Dark theme color
  light: $white // Light theme color,,
) !default;

// Page Background Color
$page-bg: #eaf1f7 !default;

// Page Paddings
$page-padding: (
  desktop: 25px,
  // Padding for desktop mode
  tablet-and-mobile: 15px // Padding for tablet and mobile modes,,
) !default;

// Header
$header-config: (
  // Desktop Mode
  desktop:
    (
      // Default Mode
      default:
        (
          height: 72px,
        ),
      // Fixed Mode
      fixed:
        (
          zindex: 97,
          height: 72px,
        )
    ),
  // Tablet & Mobile Modes
  tablet-and-mobile:
    (
      // Default Mode
      default:
        (
          height: 55px,
        ),
      // Fixed Mode
      fixed:
        (
          zindex: 97,
          height: 55px,
        )
    )
) !default;

// Header Menu
$header-menu-config: (
  // Desktop Mode
  desktop:
    (
      // Base Parameters
      base:
        (
          class: header-menu,
          // menu element class name
          mode: desktop
            // menu mode: "desktop", "desktop-and-tablet" or "general",,
        ),
      // Customize
      build:
        (
          // Layout
          layout:
            (
              // Container
              self:
                (
                  margin: 0,
                ),
              // Item
              item:
                (
                  // Item
                  self:
                    (
                      padding: 0px 0.25rem,
                    ),
                  // Link
                  link:
                    (
                      // self
                      self:
                        (
                          padding: 0.25rem 0.5rem,
                        ),
                      // Icon
                      icon:
                        (
                          width: 30px,
                          svg-width: 23px,
                          font-size: 1.4rem,
                          padding: 0,
                        ),
                      // Bullet
                      bullet:
                        (
                          self: (
                            width: 20px,
                          ),
                          dot: (
                            size: 4px,
                          ),
                          line: (
                            width: 5px,
                            height: 1px,
                          ),
                        ),
                      // Text
                      text:
                        (
                          font-size: 1rem,
                          font-weight: 400,
                          font-transform: initial,
                        ),
                      // Label
                      label:
                        (
                          padding: 0px 0px 0px 5px,
                        ),
                      // Arrow
                      arrow:
                        (
                          width: 20px,
                          align: flex-end,
                          font-size: 0.6rem,
                        )
                    ),
                  // Submenu
                  submenu:
                    (
                      // Submenu
                      self:
                        (
                          zindex: get($header-config, desktop, fixed, zindex) +
                            1,
                          padding: (
                            classic: 20px 0px,
                            megamenu: 0px,
                          ),
                          border-radius: 4px,
                          width: (
                            classic: 275px,
                            // Classic dropdown menu width
                            // full width dropdown menu
                            full:
                              (
                                offset: 20px,
                              ),
                          ),
                          animation: (
                            offset: -10px,
                          ),
                          space: 1px,
                        ),
                      // Item
                      item:
                        (
                          // Item
                          self:
                            (
                              padding: 0px,
                            ),
                          // Link
                          link:
                            (
                              // Link
                              self:
                                (
                                  padding: 11px 30px,
                                  padding-x: 30px,
                                ),
                              // Icon
                              icon:
                                (
                                  width: 33px,
                                  svg-width: 23px,
                                  font-size: 1.4rem,
                                  padding: 0,
                                ),
                              // Bullet
                              bullet:
                                (
                                  self: (
                                    width: 20px,
                                  ),
                                  dot: (
                                    size: 4px,
                                  ),
                                  line: (
                                    width: 9px,
                                    height: 1px,
                                  ),
                                ),
                              // Text
                              text:
                                (
                                  font-size: 1rem,
                                  font-weight: 400,
                                  font-transform: initial,
                                ),
                              // Label
                              label:
                                (
                                  padding: 0px 0px 0px 5px,
                                ),
                              // Arrow
                              arrow:
                                (
                                  padding: 0px 0px 0px 10px,
                                  width: 20px,
                                  font-size: 0.6rem,
                                )
                            ),
                          // Column
                          column:
                            (
                              // Column
                              self:
                                (
                                  padding: 0 0 20px 0,
                                ),
                              // Heading
                              heading:
                                (
                                  // Heading
                                  self:
                                    (
                                      padding: 30px 30px 10px 30px,
                                    ),
                                  // Caption
                                  caption:
                                    (
                                      // Icon
                                      icon:
                                        (
                                          width: 30px,
                                          svg-width: 23px,
                                          font-size: 1.35rem,
                                          padding: 0,
                                        ),
                                      // Text
                                      text:
                                        (
                                          font-size: 1.07rem,
                                          font-weight: 500,
                                          font-transform: initial,
                                        ),
                                      // Label
                                      label:
                                        (
                                          padding: 0px 0px 0px 5px,
                                        )
                                    )
                                )
                            )
                        )
                    )
                )
            ),
          // Themes
          themes:
            (
              // Light Theme
              light:
                (
                  // Item
                  item:
                    (
                      // Link
                      link:
                        (
                          // Link
                          self:
                            (
                              bg-color: (
                                default: none,
                                active: none,
                                here: none,
                                hover: none,
                              ),
                            ),
                          // Bullet
                          bullet:
                            (
                              dot: (
                                bg-color: (
                                  default: $text-muted,
                                  active: $primary,
                                  here: $primary,
                                  hover: $primary,
                                ),
                              ),
                              line: (
                                bg-color: (
                                  default: $text-muted,
                                  active: $primary,
                                  here: $primary,
                                  hover: $primary,
                                ),
                              ),
                            ),
                          // Text
                          text:
                            (
                              font-color: (
                                default: $dark-75,
                                active: $primary,
                                here: $primary,
                                hover: $primary,
                              ),
                            ),
                          // Arrow
                          arrow:
                            (
                              font-color: (
                                default: $text-muted,
                                active: $primary,
                                here: $primary,
                                hover: $primary,
                              ),
                            )
                        ),
                      // Submenu
                      submenu:
                        (
                          // Submenu
                          self:
                            (
                              bg-color: $white,
                              box-shadow: 0px 15px 50px 0px
                                rgba(82, 63, 105, 0.15),
                            ),
                          // Item
                          item:
                            (
                              link: (
                                self: (
                                  bg-color: (
                                    active: $gray-100,
                                    here: $gray-100,
                                    hover: $gray-100,
                                  ),
                                ),
                                // Icon
                                icon:
                                  (
                                    font-color: (
                                      default: $text-muted,
                                      active: $primary,
                                      here: $primary,
                                      hover: $primary,
                                    ),
                                  ),
                                // Bullet
                                bullet:
                                  (
                                    dot: (
                                      bg-color: (
                                        default: $text-muted,
                                        active: $primary,
                                        here: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                    line: (
                                      bg-color: (
                                        default: $text-muted,
                                        active: $primary,
                                        here: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                  ),
                                // Text
                                text:
                                  (
                                    font-color: (
                                      default: $dark-75,
                                      active: $primary,
                                      here: $primary,
                                      hover: $primary,
                                    ),
                                  ),
                                // Arrow
                                arrow:
                                  (
                                    font-color: (
                                      default: $text-muted,
                                      active: $primary,
                                      here: $primary,
                                      hover: $primary,
                                    ),
                                  ),
                              ),
                              // Column
                              column:
                                (
                                  // Separator
                                  separator:
                                    (
                                      border: 1px solid $gray-200,
                                    ),
                                  // Heading
                                  heading:
                                    (
                                      // Caption
                                      caption:
                                        (
                                          // Icon
                                          icon:
                                            (
                                              font-color: $text-muted,
                                            ),
                                          // Text
                                          text:
                                            (
                                              font-color: $dark,
                                            )
                                        )
                                    ),
                                ),
                            )
                        )
                    )
                ),
              // Dark Theme
              dark:
                (
                  // Item
                  item:
                    (
                      // Link
                      link:
                        (
                          // Link
                          self:
                            (
                              bg-color: (
                                default: none,
                                active: none,
                                here: none,
                                hover: none,
                              ),
                            ),
                          // Bullet
                          bullet:
                            (
                              dot: (
                                bg-color: (
                                  default: #9096b8,
                                  active: $primary,
                                  here: $primary,
                                  hover: $primary,
                                ),
                              ),
                              line: (
                                bg-color: (
                                  default: #9096b8,
                                  active: $primary,
                                  here: $primary,
                                  hover: $primary,
                                ),
                              ),
                            ),
                          // Text
                          text:
                            (
                              font-color: (
                                default: #9096b8,
                                active: $primary,
                                here: $primary,
                                hover: $primary,
                              ),
                            ),
                          // Arrow
                          arrow:
                            (
                              font-color: (
                                default: #6c7293,
                                active: $primary,
                                here: $primary,
                                hover: $primary,
                              ),
                            )
                        ),
                      // Submenu
                      submenu:
                        (
                          // Submenu
                          self:
                            (
                              bg-color: get($layout-themes, dark),
                              box-shadow: 0px 0px 50px 0px
                                rgba(82, 63, 105, 0.15),
                            ),
                          // Item
                          item:
                            (
                              link: (
                                self: (
                                  bg-color: (
                                    default: transparent,
                                    open: #1f2433,
                                    active: #1f2433,
                                    hover: #1f2433,
                                  ),
                                ),
                                // Icon
                                icon:
                                  (
                                    font-color: (
                                      default: #494b74,
                                      here: $primary,
                                      active: $primary,
                                      hover: $primary,
                                    ),
                                  ),
                                // Bullet
                                bullet:
                                  (
                                    dot: (
                                      bg-color: (
                                        default: #494b74,
                                        here: $primary,
                                        active: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                    line: (
                                      bg-color: (
                                        default: #494b74,
                                        here: $primary,
                                        active: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                  ),
                                // Text
                                text:
                                  (
                                    font-color: (
                                      default: #a2a3b7,
                                      here: $white,
                                      active: $white,
                                      hover: $white,
                                    ),
                                  ),
                                // Arrow
                                arrow:
                                  (
                                    font-color: (
                                      default: #494b74,
                                      here: $primary,
                                      active: $primary,
                                      hover: $primary,
                                    ),
                                  ),
                              ),
                              // Column
                              column:
                                (
                                  // Separator
                                  separator:
                                    (
                                      border: 1px solid rgba(#494b74, 0.25),
                                    ),
                                  // Heading
                                  heading:
                                    (
                                      // Caption
                                      caption:
                                        (
                                          // Icon
                                          icon:
                                            (
                                              font-color: #494b74,
                                            ),
                                          // Text
                                          text:
                                            (
                                              font-color: #a2a3b7,
                                            )
                                        )
                                    ),
                                ),
                            )
                        )
                    )
                )
            )
        )
    ),
