                          margin: 0,
                        ),
                      // Link
                      link:
                        (
                          // Link
                          self:
                            (
                              height: 44px,
                              padding: 9px 30px,
                            ),
                          // Icon
                          icon:
                            (
                              width: 35px,
                              font-size: 1.4rem,
                            ),
                          // Bullet
                          bullet:
                            (
                              self: (
                                width: 15px,
                              ),
                              dot: (
                                size: 4px,
                              ),
                              line: (
                                width: 5px,
                                height: 1px,
                              ),
                            ),
                          // Text
                          text:
                            (
                              font-size: 1.02rem,
                              font-weight: 400,
                              font-transform: initial,
                            ),
                          // Arrow
                          arrow:
                            (
                              width: 20px,
                              transition: all 0.3s ease,
                              font-size: 0.6rem,
                            ),
                          // Label
                          label:
                            (
                              padding: 0px 0px 0px 5px,
                              align: right,
                            )
                        ),
                      // Submenu
                      submenu:
                        (
                          // Submenu
                          self:
                            (
                              indent: 15px,
                              margin: 0,
                            ),
                          // Item
                          item:
                            (
                              // Item
                              self:
                                (
                                  margin: 0,
                                ),
                              // Link
                              link:
                                (
                                  // Link
                                  self:
                                    (
                                      height: 40px,
                                      padding: 0 30px,
                                      padding-x: 30px,
                                    ),
                                  // Icon
                                  icon:
                                    (
                                      width: 35px,
                                      font-size: 1.35rem,
                                    ),
                                  // Bullet
                                  bullet:
                                    (
                                      self: (
                                        width: 20px,
                                      ),
                                      dot: (
                                        size: 4px,
                                      ),
                                      line: (
                                        width: 5px,
                                        height: 1px,
                                      ),
                                    ),
                                  // Text
                                  text:
                                    (
                                      font-size: 1rem,
                                      font-weight: 400,
                                      font-transform: initial,
                                    ),
                                  // Arrow
                                  arrow:
                                    (
                                      width: 20px,
                                      transition: all 0.3s ease,
                                      font-size: 0.6rem,
                                    ),
                                  // Label
                                  label:
                                    (
                                      padding: 0px 0px 0px 5px,
                                    )
                                )
                            ),
                          // Section
                          section:
                            (
                              self: (
                                indent: 15px,
                                padding: 0 25px,
                                margin: 20px 0 0 0,
                                height: 40px,
                              ),
                              text: (
                                font-size: 0.8rem,
                                font-weight: 300,
                                font-transform: uppercase,
                                letter-spacing: 0.3px,
                              ),
                              icon: (
                                font-size: 1.1rem,
                              ),
                            ),
                          // Separator
                          separator:
                            (
                              margin: 15px 0,
                            )
                        )
                    ),
                  // Section
                  section:
                    (
                      self: (
                        padding: 0 27px,
                        margin: 20px 0 0 0,
                        height: 40px,
                      ),
                      text: (
                        font-size: 0.83rem,
                        font-weight: 500,
                        font-transform: uppercase,
                        letter-spacing: 0.3px,
                      ),
                      icon: (
                        font-size: 1.1rem,
                      ),
                    ),
                  // Separator
                  separator:
                    (
                      margin: 15px 0,
                    )
                )
            ),
          // Themes
          themes:
            (
              // Dark Theme
              dark:
                (
                  // Default Mode
                  default:
                    (
                      // Container
                      self:
                        (
                          bg-color: get($layout-themes, dark),
                        ),
                      // Item
                      item:
                        (
                          // Item
                          self:
                            (
                              bg-color: (
                                default: null,
                                open: null,
                                here: null,
                                active: null,
                                hover: null,
                              ),
                            ),
                          // Link
                          link:
                            (
                              // Link
                              self:
                                (
                                  bg-color: (
                                    default: transparent,
                                    open: #1f2433,
                                    here: #1f2433,
                                    active: #1f2433,
                                    hover: #1f2433,
                                  ),
                                ),
                              // Icon
                              icon:
                                (
