              // Minimize Mode
              minimize:
                (
                  // Item
                  item:
                    (
                      // Link
                      link:
                        (
                          // Link
                          self:
                            (
                              bg-color: (
                                default: null,
                                open: null,
                                active: null,
                                hover: null,
                              ),
                            ),
                          // Icon
                          icon:
                            (
                              font-color: (
                                default: $text-muted,
                                open: $primary,
                                active: $primary,
                                hover: $primary,
                              ),
                            )
                        )
                    )
                ),
              // Dropdown Mode
              dropdown:
                (
                  // Item
                  item:
                    (
                      // Submenu
                      submenu:
                        (
                          // Submenu
                          self:
                            (
                              // submenu base styles
                              bg-color: $white,
                              box-shadow: 0px 0px 50px 0px
                                rgba(82, 63, 105, 0.15)
                            ),
                          // Item
                          item:
                            (
                              // Item
                              self:
                                (
                                  bg-color: (
                                    default: null,
                                    open: null,
                                    here: null,
                                    active: null,
                                    hover: null,
                                  ),
                                ),
                              // Link
                              link:
                                (
                                  // Link
                                  self:
                                    (
                                      bg-color: (
                                        default: null,
                                        open: $gray-100,
                                        here: $gray-100,
                                        active: $gray-100,
                                        hover: $gray-100,
                                      ),
                                    ),
                                  // Icon
                                  icon:
                                    (
                                      font-color: (
                                        default: $text-muted,
                                        open: $primary,
                                        active: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                  // Bullet
                                  bullet:
                                    (
                                      dot: (
                                        bg-color: (
                                          default: $text-muted,
                                          open: $primary,
                                          here: $primary,
                                          active: $primary,
                                          hover: $primary,
                                        ),
                                      ),
                                      line: (
                                        bg-color: (
                                          default: $text-muted,
                                          open: $primary,
                                          here: $primary,
                                          active: $primary,
                                          hover: $primary,
                                        ),
                                      ),
                                    ),
                                  // Text
                                  text:
                                    (
                                      font-color: (
                                        default: $dark-75,
                                        open: $primary,
                                        active: $primary,
                                        hover: $primary,
                                      ),
                                    ),
                                  // Arrow
                                  arrow:
                                    (
                                      font-color: (
                                        default: $text-muted,
                                        open: $primary,
                                        active: $primary,
                                        hover: $primary,
                                      ),
                                    )
                                )
                            ),
                          // Section
                          section:
                            (
                              text: (
                                font-color: $dark-50,
                              ),
                              icon: (
                                font-color: $text-muted,
                              ),
                            ),
                          // Separator
                          separator:
                            (
                              border: 1px solid $gray-200,
                            )
                        )
                    )
                )
            )
        )
    )
) !default;

// Sidebar
$sidebar-config: (
  // Sidebar
  base:
    (
      zindex: 96,
      scroll-width: 4px,
      shadow: 0px 0px 28px 0px rgba(82, 63, 105, 0.13),
      bg-color: $white,
      transition-speed: 0.3s ease,
      border-color: $gray-200,
      width: 350px,
      toolbar-width: 65px,
    ),
  // Offcanvas Mobile
  offcanvas-mobile:
    (
      width: 300px,
      zindex: 98,
      bg-color: $white,
      shadow: 0px 1px 9px -3px rgba(0, 0, 0, 0.75),
      overlay-bg-color: rgba(#000000, 0.1),
    )
) !default;

// Subheader
$subheader-config: (
  // Default Mode
  default:
    (
      bg-color: $white,
      // Subheader's solid background color
      border-color: $gray-200,
      // Subheader's separator border color
    ),
  // Fixed Subheader Mode
  fixed:
    (
      height: 54px,
      // Fixed subheader's height
      height-mobile: 65px,
      // Fixed mobile subheader's height
      shadow: 0px 10px 30px 0px rgba(82, 63, 105, 0.08),
      // Fixed subheader's box shadow
      zindex: 95 // Fixed subheader's z-index,,
    )
) !default;

// Footer
$footer-config: (
  // Fixed Footer Mode
  fixed:
    (
      zindex: 97,
      // Fixed footer's z-index
      height: 60px,
      // Fixed footer's height
      shadow: 0px 0px 28px 0px rgba(82, 63, 105, 0.13)
        // Fixed fooer box shadow,,
    )
) !default;
